/**********/

/* GLOBAL */

/**********/

/* Box Model Fix */

html {
	box-sizing: border-box;
	background-color: white;
}

html,
body {
	height: 100%;
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

img {
	width: 100%;
}

a {
	text-decoration: none;
	outline: none;
}

/* Clearfix */

.group:after {
	content: "";
	display: table;
	clear: both;
}

/* Vertically Center Content */

.center-content {
	position: relative;
	top: 50%;
	transform: translateY(-50%);
}

/*****************/

/* SITE SPECIFIC */

/*****************/

/* RBDGD Colors */

$orange: #c96420;
$tan: #ded3b1;
$drkblue: #3d4b5d;

/* Fonts */

$base-font: "Open Sans",
sans-serif;
$light: 300;
$italic: 400i;
$black: 800;

/* Typography */

body {
	font-family: $base-font;
}

p,
h3,
h4 {
	margin: 15px 0;
}

h3,
h4 {
	font-weight: $black;
}

p {
	line-height: 150%;
}

h2 {
	font-size: 3em
}

h3 {
	font-size: 2em;
}

h4 {
	text-transform: uppercase;
}

h5 {
	font-size: 1.5em;
}

/* Background Styles */

.bg-full {
	height: 100%;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
}

.bg-width {
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: top;
	background-color: white;
}

.bg-homepage {
	background-image: url(../images/rbdgd-bg-sm.jpg);
}

.bg-product {
	background-image: url(../images/crystal-sky.svg);
}

/* Homepage */

.homepage-splash {
	margin-top: -150px;
}

/* Navigation */

.nav_main-menu {
	width: 100%;
	background: $orange;
	position: fixed;
	z-index: 100;
}

.nav-table {
	display: table;
	/* Use this code to horizontally centre an inline list */
	margin: 0 auto;
	text-align: center;
}

.nav-table ul li {
	display: inline-block;
	width: 200px;
	margin: 0 20px;
	text-align: center;
}

/*Simple Ghost Button Component */

.nav-btn {
	width: 100%;
	float: left;
	line-height: 40px; //To vertically centre a single line of text
	border: 1px solid;
	text-align: center;
	text-transform: uppercase;
	margin: 10px 0;

	transition: background-color 0.15s ease-in-out; //Note: consider a mixin for these!
	-moz-transition: background-color 0.15s ease-in-out;
	-webkit-transition: background-color 0.15s ease-in-out;
}

.nav-btn:focus,
.nav-btn:hover {
	background: rgba(255, 255, 255, 0.5);
}

.nav-btn_blue {
	border-color: #3d4b5d;
	color: #3d4b5d;
}

.nav-btn_white {
	border-color: white;
	color: white;
}

.nav-list li {
	display: inline;
	width: 20%;
	float: left;
	padding: 5px;
}

.nav_home-logo {
	width: 50px;
	margin: 5px auto;
	display: block;
}

/* Text Box Styles */

.text-box {
	padding: 100px 0;
}

.text-box_small {
	padding: 25px;
	margin-top: 3em;
	margin-bottom: 3em;
	text-align: center;
}

.text-box_align {
	text-align: right;
}

.text-box_images {
	position: relative;
}

.text-box_bg-white {
	background-color: none;
}

.text-box_bg-white h3 {
	color: $orange;
}

.text-box_bg-white h4 {
	color: $drkblue;
}

.text-box_bg-blue {
	background-color: $drkblue;
	color: white;
}

.text-box_bg-blue h4 {
	color: $orange;
}

.text-box_bg-orange {
	background-color: $orange;
	color: white;
}

.text-box_border-blue {
	border: 1px solid $drkblue;
}

.text-box_svg {
	padding: 0 20% 5% 20%;
}

.text-box_jpg {
	padding-right: 10%;
	padding-top: 15px;
}

.text-box_png {
	position: relative;
	margin-bottom: -25%;
	z-index: 100;
}

/* Feature Styles */

.feature_title {
	text-align: center;
	margin-top: 7%;
}

.feature_title h2 {
	font-weight: $black;
	color: $drkblue;
	text-transform: uppercase;
	letter-spacing: 0.2em;
}

.feature_title h5 {
	color: gray;
	text-transform: uppercase;
	letter-spacing: 0.3em;
	margin-bottom: 48px;
}

.feature_subtitle {
	border-bottom: 1px solid $drkblue;
}

.feature_subtitle h3 {
	color: $orange;
	text-align: right;
}

/* Scroll Button */

.scroll-btn {
	display: block;
	width: 100%;
	font-size: 2em;
	text-align: center;
}

.scroll-btn_white {
	color: white;
}

.scroll-btn_orange {
	color: $orange;
}

/* Sticky Footer Navigation */

.nav-bottom {
	display: none;
}

.nav-bottom_menu {
	position: fixed;
	left: 0;
	bottom: 0;
	width: 100%;
	background-color: $orange;
	color: white;
	text-align: center;
	font-size: 30px;
}

.nav-bottom_menu li {
	display: inline-block;
	width: 20%;
	line-height: 50px;
	text-align: center;
}

.nav-bottom_menu a {
	float: left;
	width: 100%;
	color: white;
}

.nav-bottom_menu a:focus,
.nav-bottom_menu a:hover {
	opacity: 0.5;
	filter: alpha(opacity=50);
	/* For IE8 and earlier */
}

.nav-bottom_menu img {
	display: block;
	width: 30px;
	margin: 10px auto;
}

/*****************/

/* MEDIA QUERIES */

/*****************/

/* Min Width 768px */

@media only screen and (min-width: 768px) {
	html {
		background: url(../images/mountains.svg) no-repeat center center fixed;
		-webkit-background-size: cover;
		-moz-background-size: cover;
		-o-background-size: cover;
		background-size: cover;
	}
	.bg-homepage {
		background-image: url(../images/rbdgd-bg.jpg);
	}
}

/* Max Width 1025px */

@media only screen and (max-width: 1025px) {
	.homepage-splash {
		margin-top: -100px;
	}

	.nav-list li {
		display: none;
	}

	.text-box {
		padding: 50px 0;
	}
	.text-box_png {
		margin-bottom: 0;
	}
	.text-box_jpg {
		padding-right: 0;
		padding-bottom: 24px;
	}
	.text-box_align {
		text-align: left;
	}

	.feature_title {
		margin-top: 0;
	}
	.feature_title h2 {
		letter-spacing: initial;
	}
	.feature_title h5 {
		letter-spacing: initial;
		margin-bottom: 24px;
	}
	.feature_subtitle {
		border: none;
		margin-bottom: 24px;
	}
	.feature_subtitle h3 {
		text-align: center;
	}

	.pic-resize {
		display: block;
		width: 75%;
		margin: 0 auto;
	}
	.nav-bottom {
		display: block;
	}
}

/* Junk */

.text-box_small img {
	margin-top: -200px;
}

.text-box_small {
	margin-top: 200px;
}
